#blockList{
    overflow: hidden;
    //top: 5vh;
}
#contentForm{
    .row{
        > div{
            min-width: 30%;
            padding: 20px;
        }
    }
}
#templateList{
    height: 100%;
    overflow-y: scroll;
    width: calc(100% - 20px);
    max-width: calc(25% - 20px);
    margin-right: 20px;
    padding-right: 10px;
    max-height: 90vh;
    position: sticky;
    top: 5vh;
    &::-webkit-scrollbar {
        width: 4px;
    }

    &::-webkit-scrollbar-track {
        background: #D6D6D6;
        width: 4px;
        border-right: 1px solid white;
        border-left: 1px solid white;
        background-clip: content-box;
    }

    &::-webkit-scrollbar-thumb {
        background: #1D8FBD;
        width: 4px;
    }
    .template-item{
        img{
            width: 100%;
        }
    }
}
.admin-template-row{
    //align-items: flex-start;
    //overflow: hidden;
}
.sidebar-dark-primar{
    background-color: #132238!important;
}
.badge-success{
    background-color: white;
    color: #132238;
}
.has-treeview{
    .nav-treeview{
        i{
            color: transparent;
        }
    }

}
.field-iteration, .field-repeater{
    border: 1px solid #ced4da;
    padding: 1rem;
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 1rem;
    > .field-input{
        width: 100% !important;
    }
    >.field-repeater{
        padding-top: 0!important;
        padding-bottom: 0!important;
    }
    >.field-iteration, >.field-repeater{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        width: 100%;
        padding: 0 0 1rem 0;
        border: none;
        margin-bottom: 0;
        > div{
            width: calc(((100% - 40px) / 3) - 20px);
            @media all and (max-width: 768px){
                width: 100%;
            }
        }
        > .field-input{
            width: 32% !important;
            @media all and (max-width: 768px){
                width: 100% !important;
            }
        }
    }

}
.card-body{
    > .add-iteration{
        width: 100%;
    }
}
.form-group.field-image{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    label{
        width: 100%;
    }
    img{
        max-height: 100px;
        margin-right: 1rem;
    }
    .input-group{
        width: 50%;
    }
}
.remove-iteration{
    margin-bottom: 1rem;
}
.programs-field{
    .add-iteration{
        //margin-top: 1rem;
    }
    &.field-repeater, &.field-iteration{
        .field-repeater{

        }
        .form-group{
            margin-bottom: 1rem;
        }
        .field-repeater, .field-iteration{
            padding-bottom: 0;
            label{
                display: none;
            }
            &:nth-child(2){
                label{
                    display: inline-block;
                }
            }
        }
    }
}
.module-create-items{
    .input-group{
        label{
            width: 100%;
        }
    }

    .module-attributes{
        border: 1px solid #ced4da;
        box-shadow: 0 0 10px 0 rgb(0 23 46 / 10%);
        padding: 1rem 1rem 0 1rem;
        margin-bottom: 1rem;
    }
}
.nav-sidebar .nav-header{
    font-size: .9rem;
    padding: 0.5rem!important;
    display: flex;
    align-items: flex-end;
    opacity: 0.5;
    span{
        //white-space: nowrap;
        display: block;
        margin-right: 0.5rem;
        opacity: 0.5;
    }
    //&:after{
    //    content: "";
    //    display: block;
    //    flex: 1;
    //    height: 1px;
    //    background-color: white;
    //    opacity: 0.2;
    //}
}
.brand-link{
    padding-bottom: 0!important;
    .brand-image{
        opacity: 1!important;
        max-height: none!important;
        width: 100% !important;
        margin-left: 1rem!important;
        margin-right: 1rem!important;
        &.elevation-3{
            box-shadow: none!important;
        }
    }
}
.module-creator-item{
    .btn-group-vertical>.btn, .btn-group>.btn{
        flex: 0!important;
    }
    .btn-group{
        width: 100%;
        display: flex;
        justify-content: flex-end;
    }
}
.icheck-primary-items{
    > label{
        display: block;
    }
    .icheck-primary{
        span{
            background-color: #D9D9D9;
            padding: 10px 20px;
            margin-right: 10px;
            margin-bottom: 10px;
            border-radius: 26px;
            font-weight: 400;
            display: inline-block;
            text-transform: uppercase;
        }
        input{
            display: none;
            &:checked + span{
                background-color: #132238;
                color: white;
            }
        }
    }
}

